import { newSpecPage } from '@stencil/core/testing';
import { PrestoTimestamp } from '../presto-timestamp';

describe('presto-timestamp', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoTimestamp],
      html: `<presto-timestamp time="1:00"></presto-timestamp>`,
    });
    expect(page.root).toMatchSnapshot();
  });
  it('renders custom content', async () => {
    const page = await newSpecPage({
      components: [PrestoTimestamp],
      html: `<presto-timestamp time="1:00">Custom Content</presto-timestamp>`,
    });
    expect(page.root).toMatchSnapshot();
  });
  it('timeToSeconds', async () => {
    const component = new PrestoTimestamp();
    expect(component.timeToSeconds('1:00')).toBe(60);
    expect(component.timeToSeconds('12:34')).toBe(754);
  });
});
